import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './Users.module.css';

let User = ({user,folowingInProgress,unfollow,follow}) => {
    return (
            <li className={s.li} key={user.id}>
                <span>
                    <NavLink  to={'/profile/'+user.id} >
                        <img className={s.img} src={user.photos.small != null?user.photos.small:'https://www.google.com/url?sa=i&url=https%3A%2F%2Fwww.flaticon.com%2Ffree-icon%2Fuser_149071&psig=AOvVaw3BbMOTtsvxCzsJAB3lKh1r&ust=1680189991593000&source=images&cd=vfe&ved=0CBAQjRxqFwoTCLjYs8-5gf4CFQAAAAAdAAAAABAE'} alt="ava"/>
                    </NavLink>
                    {user.followed
                    ?
                        <button disabled={folowingInProgress.some(id => id === user.id)} 
                            onClick={() => {unfollow(user.id)}}
                        > Unfollow</button>
                    :
                        <button disabled={folowingInProgress.some(id => id === user.id)} 
                            onClick={() => {follow(user.id)}}
                        >Follow</button>
                    }     
                </span>
                <span>
                    <b>{user.name}</b>
                    <p>{user.status}</p>
                    <span>{"user.location.city"}</span>
                    <span>{"user.location.country"}</span>
                </span>
            </li>
    )
}

export default User;